// import $ from 'jquery'
import { ref } from 'vue'
import { defineStore } from 'pinia'
import $ from 'jquery'
import { jwtDecode } from "jwt-decode";

export const useCounterStore = defineStore('user', () => {
    // const count = ref(0)
    // const doubleCount = computed(() => count.value * 2)
    // function increment() {
    //     count.value++
    // }


    //全局变量
    let user_id = ref('')
    let user_username = ref('')
    let user_photo = ref('')
    let user_followerCount = ref(0)
    let user_access = ref('')
    let user_refresh = ref('')
    let user_is_login = ref(false)

    function updateUser(user) {
        user_id.value = user.id;
        user_username.value = user.username;
        user_photo.value = user.photo;
        user_followerCount.value = user.followerCount;
        user_access.value = user.access;
        user_refresh.value = user.refresh;
        user_is_login.value = user.is_login;
    }
    function updateAccess(access) {
        user_access.value = access;
    }
    function logout() {
        user_id.value = "";
        user_username.value = "";
        user_photo.value = "";
        user_followerCount.value = 0;
        user_access.value = "";
        user_refresh.value = "";
        user_is_login.value = false;
    }

    function getToken(data) {
        $.ajax({
            url: "https://app165.acapp.acwing.com.cn/api/token/",
            type: "POST",
            data: {
                username: data.username,
                password: data.password,
            },
            success(resp) {
                const { access, refresh } = resp;
                const access_obj = jwtDecode(access);

                setInterval(() => {
                    $.ajax({
                        url: "https://app165.acapp.acwing.com.cn/api/token/refresh/",
                        type: "POST",
                        data: {
                            refresh,
                        },
                        success(resp) {
                            updateAccess(resp.access);
                        }
                    });
                }, 4.5 * 60 * 1000);
                $.ajax({
                    url: "https://app165.acapp.acwing.com.cn/myspace/getinfo/",
                    type: "GET",
                    data: {
                        user_id: access_obj.user_id,
                    },
                    headers: {
                        'Authorization': "Bearer " + access,
                    },
                    success(resp) {
                        updateUser({
                            ...resp,
                            access: access,
                            refresh: refresh,
                            is_login: true,
                        });
                        data.success();
                        console.log(user_id.value);
                    },
                })
            },
            error() {
                data.error();
                console.log('123');
            }
        })
    }







    return { user_is_login, user_username, logout, getToken }

})
