<template>
    <ContentBase>
        <div class="container">
            <div class="row">
                <div class="content-right ">

                    <div class="row justify-content-md-center">
                        <div class="col-6">
                            <form class="row g-3 was-validated" @submit.prevent="register">
                                <div class="col-md-6">
                                    <label for="model" class="form-label">手机号</label>
                                    <input class="form-control is-invalid" id="mobile" type="model"
                                        onkeyup="value=value.replace(/[^\d]/g,'')" maxlength=11 required>

                                </div>
                                <div class="col-md-6">
                                    <label for="email" class="form-label">电子邮箱</label>
                                    <input type="email" class="form-control" id="email" required>
                                </div>
                                <div class="col-md-6">
                                    <label for="name" class="form-label">姓名</label>
                                    <input type="text" class="form-control" id="name" required>
                                </div>
                                <div class="col-md-6">
                                    <label for="local" class="form-label">工作单位</label>
                                    <input type="text" class="form-control" id="local" required>
                                </div>
                                <div class="col-md-6">
                                    <label for="username" class="form-label">用户名</label>
                                    <input v-model="username" type="text" class="form-control" id="username" maxlength="11"
                                        required>
                                </div>
                                <div class="col-md-6">
                                    <label for="password" class="form-label">密码</label>
                                    <input v-model="password" type="password" class="form-control" id="password" required>
                                </div>
                                <div class="col-md-12">
                                    <label for="password_confirm" class="form-label">确认密码</label>
                                    <input v-model="password_confirm" type="password" class="form-control"
                                        id="password_confirm">
                                </div>
                                <div class="error-message">{{ error_message }}</div>
                                <button type="submit" class="btn btn-primary">注册</button>
                            </form>


                        </div>
                    </div>

                </div>
            </div>
        </div>
    </ContentBase>
</template>

  
<script setup>
import ContentBase from '@/components/ContentBase.vue';
import { ref } from 'vue';
import { useCounterStore } from '../stores/user';
import router from '@/router/index';
import $ from 'jquery';



const countStore = useCounterStore()
let username = ref('');
let password = ref('');
let password_confirm = ref('');
let error_message = ref('');

const register = () => {
    error_message.value = "";
    $.ajax({
        url: "https://app165.acapp.acwing.com.cn/myspace/user/",
        type: "POST",
        data: {
            username: username.value,
            password: password.value,
            password_confirm: password_confirm.value,
        },
        success(resp) {
            if (resp.result === "success") {
                countStore.getToken({
                    username: username.value,
                    password: password.value,
                    success() {
                        router.push({ name: 'home' });
                    },
                    error() {
                        error_message.value = "系统异常，请稍后重试";
                    }
                });
            } else {
                error_message.value = resp.result;
            }
        }
    })
}
</script>
  
<style scoped>
.row {
    /* box-sizing: border-box; */
    display: flex;
    flex-wrap: wrap;
    font-family: Helvetica-Bold, Helvetica;
    margin-top: 30px;
}



.content-right {
    height: 100%;
    background-color: white;
    min-height: 100px;
    flex-shrink: 3;
    border-radius: 1.674074px;
    font-family: Helvetica-Bold, Helvetica;
}

.btn {
    width: 100%;
    margin-bottom: 20px;
}

.error-message {
    color: red;
}
</style>